.navbar .sign_in_btn {
    background: var(--button-color);
    color: #000000;
    margin-right: 10px;
    padding: 8px 25px
}

.navbar .register_btn {
    background: #DB595A;
    padding: 8px 25px;
    color: #FFFFFF;
}

.header_navbar {
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 999;
    padding: 20px 0;
}

.home_wrapper {
    background-image: url(../images/home-bg.png);
    height: auto;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 22;
    position: relative;
    width: 100%;
    min-height: 92vh;
}

.top-banner-wrapper h2 {
    font-weight: 700;
    font-size: 70px;
    color: #062a4d;
    line-height: 74px;
}

.top-banner-wrapper p {
    font-size: 22px;
    color: #062a4d;
    padding: 30px 0 20px;
}

.top-banner-wrapper p span {
    color: #000;
}

.btn-landing {
    background: #db595a;
    box-shadow: 0px 3px 13px rgb(0 0 0 / 16%);
    font-weight: 500;
    font-size: 21px;
    color: #FFFFFF;
    padding: 10px 50px;
    border-radius: 0;
}

.bg-yellow {
    background: #f0f7fc;
}

.bg-red {
    background: #FFDADA;
}

.bg-white {
    background: #ffffff;
}

.p-5 {
    padding: 5rem 0 5rem 0;
}

.section-gap {
    padding: 80px 0;
}

.section-title {
    padding-bottom: 50px;
}

.section-title h3 {
    color: #062a4d;
    font-size: 42px;
    font-weight: 600;
    text-transform: uppercase;
}

.section-title p {
    font-size: 24px;
}

.card-wrapper {
    background: white;
    box-shadow: rgb(17 12 46 / 15%) 0px 48px 100px 0px;
    padding: 20px 15px;
    border-radius: 5px;
    margin-bottom: 34px;
}

.card-body-content p {
    font-size: 18px;
    color: #062a4d;
    padding: 35px 0 0;
}

.company_list_wrapper img {
    width: 100px;
    margin: 0 auto;
}

.features_wrapper {
    text-align: center;
}

.features_wrapper img {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    object-fit: contain;
}

.signup_wrapper {
    background-image: url(../images/bg-11.jpg);
    height: 400px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.signup_wrapper .section-title {
    padding-bottom: 0;
    display: grid;
    place-items: center;
    height: 270px;
    place-content: center;
    max-width: 900px;
    margin: 0 auto;
}

.signup_wrapper .section-title h3 {
    margin-bottom: 20px;
}

.features_wrapper .features_content_iinner p {
    -webkit-line-clamp: 3;
    position: relative;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    min-height: 100px;
}

.features_wrapper .card-body-content h5 {
    min-height: 80px;
}

section.top-banner-wrapper {
    text-align: left;
    height: 100%;
    padding-top: 270px;
}

.footer {
    padding: 25px;
    background: #062a4d;
    color: white;
    font-size: 18px;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}
    