.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked+.slider {
  background-color: #aa504f;
}

input:focus+.slider {
  box-shadow: 0 0 1px #aa504f;
}

input:checked+.slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* The custom-radio */

.custom-radio {
  display: initial;
  position: relative;
  padding-left: 35px;
  padding-top: 5px;
  cursor: pointer;
  user-select: none;
}

/* Hide the browser's default radio button */

.custom-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.posts-wrapper {
  text-align: left;
}

/* Create a custom radio button */

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */

.custom-radio:hover input~.checkmark {
  background-color: #ccc;
}

/* When the radio button is checked, add a blue background */

.custom-radio input:checked~.checkmark {
  background-color: #aa504f;
}

/* Create the indicator (the dot/circle - hidden when not checked) */

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */

.custom-radio input:checked~.checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */

.custom-radio .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

/* Input file */

.file-upload {
  display: block;
  text-align: center;
  font-size: 12px;
}

.file-upload .file-select {
  display: block;
  border: 2px solid #aa504f;
  color: #fff;
  cursor: pointer;
  height: 50px;
  line-height: 50px;
  text-align: left;
  background: #ffffff;
  overflow: hidden;
  position: relative;
}

.file-upload .file-select .file-select-button {
  background: #aa504f;
  padding: 0 10px;
  display: inline-block;
  height: 50px;
  line-height: 50px;
}

.file-upload .file-select .file-select-name {
  line-height: 50px;
  display: inline-block;
  padding: 0 10px;
  color: #000000;
}

.file-upload.active .file-select {
  border-color: #3fa46a;
  transition: all 0.2s ease-in-out;
}

.file-upload.active .file-select .file-select-button {
  background: #3fa46a;
  color: #ffffff;
  transition: all 0.2s ease-in-out;
}

.file-upload .file-select input[type="file"] {
  z-index: 100;
  cursor: pointer;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  filter: alpha(opacity=0);
}

.file-upload .file-select.file-select-disabled {
  opacity: 0.65;
}

.file-upload .file-select.file-select-disabled:hover {
  cursor: default;
  display: block;
  border: 2px solid #dce4ec;
  color: #34495e;
  cursor: pointer;
  height: 50px;
  line-height: 50px;
  margin-top: 5px;
  text-align: left;
  background: #ffffff;
  overflow: hidden;
  position: relative;
}

.file-upload .file-select.file-select-disabled:hover .file-select-button {
  background: #dce4ec;
  color: #666666;
  padding: 0 10px;
  display: inline-block;
  height: 50px;
  line-height: 50px;
}

.file-upload .file-select.file-select-disabled:hover .file-select-name {
  line-height: 50px;
  display: inline-block;
  padding: 0 10px;
}

