.login-form button,
.forget-form button {
  background-color: #aa504f;
}

.forget-password a,
.forget-password span {
  color: #6e474a !important;
  font-weight: 600;
}

.forget-password a:hover {
  color: #6e474a !important;
}

input.form-control,
textarea.form-control {
  border-radius: 10px;
}

label.form-check-label {
  font-size: 17px;
}

.errorMessage {
  color: red;
  width: 100%;
  display: inline-block;
  text-align: left;
  padding-left: 10px;
  font-weight: 500;
}

.form-check.form-check-inline {
  margin-bottom: 20px;
}

input.form-check-input {
  -ms-transform: scale(1.5);
  -webkit-transform: scale(1.5);
  transform: scale(1.5);
}

.form-check-inline .form-check-input {
  margin-right: 0.6rem;
}

input.form-check-input:after {
  width: 15px;
  height: 15px;
  border-radius: 15px;
  top: -2px;
  left: -1px;
  position: relative;
  background-color: #d1d3d1;
  content: "";
  display: inline-block;
  visibility: visible;
  border: 2px solid white;
}

input.form-check-input:checked:after {
  width: 15px;
  height: 15px;
  border-radius: 15px;
  top: -2px;
  left: -1px;
  position: relative;
  background-color: #aa504f;
  content: "";
  display: inline-block;
  visibility: visible;
  border: 2px solid white;
}

